<template>
    <div class="register">
        <div class="register-title">
            新用户注册
        </div>
        <div class="input-wrap">
            <el-input placeholder="请输入用户名" v-model="formData.username" ></el-input>
        </div>
        <div class="input-wrap">
            <el-input placeholder="请输入邮箱" v-model="formData.email" ></el-input>
        </div>
        <div class="input-wrap">
           <el-input placeholder="请输入密码" type="password" v-model="formData.password" ></el-input>
        </div>
        <div class="btn-wrap">
            <el-button type="primary" @click="handleRejister" class="btn">注册</el-button>
        </div>
    </div>   
</template>

<script>
    export default {
        data() {
            return {
                formData: {
                    username:'111',
                    email:'111',
                    password:'111'
                }
            }
        },
        methods:{
            handleRejister(){
                this.$axios.post('/user', this.formData).then(res => {
                    if(res.code == 200){
                        this.$message.success(res.msg)
                        setTimeout(()=> {
                            this.$router.push('/index')
                        },500)
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            }
        }
    }
</script>

<style scoped>
    .register{
        width: 500px;
        margin: 90px auto 0;
        padding: 40px;
        background: #fff;
        border-radius: 6px;

    }
    .register .register-title{
        text-align: center;
        color: #333;
        font-size: 24px;
        font-weight: 600;
        
    }
    .input-wrap{
        margin:30px auto;
        width: 400px;
    }
    .btn-wrap{
        margin:30px auto;
        width: 400px;
    }
    .btn{
        width: 400px;
    }
</style>


